.details{
    display: flex;
    flex-direction: row;
    height: 100%;
    margin: 10px 0px 10px 20px;
}

.details_container {
    width: 80%;
    padding: 60px 50px 20px 50px;
}

@media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .details_container {
      padding: 20px 10px 20px 10px;
    }
  }
  

.member_image {
    width: 170px;
    height: 180px;
    object-fit: cover;
    border-radius: 10px;
    margin-right: 30px;

}

.details_container_top {
    display: flex;
    flex-direction: row;
    align-items: center;
}

@media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .details_container_top {
      flex-direction: column;
      text-align: center;
    }
}

.details_row {
    display: flex;
    flex-direction: column;
}

.member_socials {
    display: flex;
    flex-direction: row;
    margin-top:5px;
}

.social_image {
    width:35px;
    height:35px;
    margin-right: 5px;
}

.member_name {
    font-style: normal;
    font-weight: normal;
    font-size: 45px;
    line-height: 61px;
}

.member_title {
    font-style: normal;
    font-weight: normal;
    font-size: 32px;
    line-height: 46px;

    /* Google Grey */

    color: #666C73;
}

.member_tags {
    font-size: 35px;
    line-height: 35px;
    color: #000000;
    margin: 20px 0px 20px 0px;
    text-transform: capitalize;
}

@media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .member_tags {
        font-size: 4vh;
    }

    .member_socials{
        align-items:center;
        justify-content: center;
    }
}

.member_description {
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 31px;

    /* Google Grey */

    color: #666C73;
}

.hovermousepointer:hover {
    cursor: pointer;
}
.sidenav_elem{
